"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { useLanguage } from "@/components/LanguageProvider";

const CreateMediaForm = ({ onSubmit, isLoading, error, initialData, isEditMode = false }) => {
  const router = useRouter();
  const { t, lang } = useLanguage();
  const [selectedFile, setSelectedFile] = useState(null);
  const [filePreview, setFilePreview] = useState(null);

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setSelectedFile(file);
      
      // Create preview for images
      if (file.type.startsWith('image/')) {
        const reader = new FileReader();
        reader.onload = (e) => {
          setFilePreview(e.target.result);
        };
        reader.readAsDataURL(file);
      } else {
        setFilePreview(null);
      }
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (!isEditMode && !selectedFile) {
      alert(t("media.selectFile"));
      return;
    }

    // For create mode, just pass the file
    // For edit mode, we don't need to pass any data since we're not updating names
    if (isEditMode) {
      onSubmit({}); // Empty object for edit mode
    } else {
      onSubmit({}, selectedFile); // Pass file for create mode
    }
  };

  return (
    <div className="bg-white shadow sm:rounded-lg">
      <div className="px-4 py-5 sm:p-6">
        <form onSubmit={handleSubmit}>
          <div className="space-y-6">
            {/* File Upload */}
            {!isEditMode && (
              <div>
                <label htmlFor="file" className={`block text-sm font-medium text-gray-700 ${
                  lang === "ar" ? "text-right" : "text-left"
                }`}>
                  {t("media.form.file")}
                </label>
                <div className="mt-1">
                  <input
                    type="file"
                    id="file"
                    onChange={handleFileChange}
                    className={`block w-full text-sm text-gray-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-indigo-50 file:text-indigo-700 hover:file:bg-indigo-100 ${
                      lang === "ar" ? "text-right" : "text-left"
                    }`}
                    accept="image/*,video/*,application/pdf"
                  />
                </div>
                
                {/* File Preview */}
                {filePreview && (
                  <div className="mt-2">
                    <img 
                      src={filePreview} 
                      alt="Preview" 
                      className="max-w-xs max-h-32 object-contain border rounded"
                    />
                  </div>
                )}
                
                {selectedFile && (
                  <p className="mt-1 text-sm text-gray-500">
                    {t("media.selectedFile")}: {selectedFile.name} ({(selectedFile.size / 1024 / 1024).toFixed(2)} MB)
                  </p>
                )}
              </div>
            )}

            {/* Form Actions */}
            <div className="flex justify-end space-x-3">
              <button
                type="button"
                onClick={() => router.push("/admin/dashboard")}
                className="admin-button admin-button-secondary"
                disabled={isLoading}
              >
                {t("media.form.cancel")}
              </button>
              <button
                type="submit"
                className="admin-button admin-button-primary"
                disabled={isLoading}
              >
                {isLoading ? (
                  <div className="flex items-center">
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                    {isEditMode ? t("media.form.update") : t("media.form.submit")}
                  </div>
                ) : (
                  isEditMode ? t("media.form.update") : t("media.form.submit")
                )}
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreateMediaForm; 